.react-fancybox .thumbnail img {
    max-width: 25px;
}

.MuiButton-startIcon {
     margin-left: 0px;
     margin-right: 0px;
}
.MuiButton-label {
    justify-content: center;
}
.MuiButton-root {
     min-width: 0px;
}

.button__div{
    color: #8b008b;
    border-radius: 50%;
    margin: 0px auto;
    width: 30px;
    height: 30px;
    font-weight: 500;
    line-height: 1.75;
}
.button__div:hover{
    background-color: #ffb2ff;
    margin-left: 0px;
    margin-right: 0px;
}

.button__icon{
    /*width: 150px;*/
    font-size: 20px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
}

.button__icon:hover{
    transition: .3s;
    transform: rotate(360deg);
}


